import React from "react";
import numeral from "numeral";
import { Circle, Popup } from "react-leaflet";

// object assigned to cases, recovered, and deaths
const casesTypeColors = {
    cases: {
        hex: "#CC1034",
        multiplier: 800,
    },
    recovered: {
        hex: "#7dd71d",
        multiplier: 1200,
    },
    deaths: {
        hex: "#fb4443",
        multiplier: 2000,
    },
};

// helper function to create sorted data with .sort
// return -1 is false and 1 is true
export const sortData = (data) => {
    let sortedData = [...data];
    sortedData.sort((a, b) => {
        if (a.cases > b.cases) {
            return -1;
        } else {
            return 1;
        }
    });
    return sortedData;
};

export const prettyPrintStat = (stat) =>
    stat ? `+${numeral(stat).format("0.0a")}` : "+0";

// draw circles on map with interactive tooltip
// takes center of circle from country lat and long
// uses Math.sqrt to get a number and multiplies it by
// the multiplier in the casesTypeColors object and uses that
// as the radius to determine size of the circle
// creates popup of flag, country, cases, recoveries, deaths when clicked
export const showDataOnMap = (data, casesType = "cases") =>
    data.map((country) => (
        <Circle
            center={[country.countryInfo.lat, country.countryInfo.long]}
            color={casesTypeColors[casesType].hex}
            fillColor={casesTypeColors[casesType].hex}
            fillOpacity={0.4}
            radius={
                Math.sqrt(country[casesType]) * casesTypeColors[casesType].multiplier
            }
        >
            <Popup>
                <div className="covidinfo-container">
                    <div
                        className="covidinfo-flag"
                        style={{ backgroundImage: `url(${country.countryInfo.flag})` }}
                    ></div>
                    <div className="covidinfo-name">{country.country}</div>
                    <div className="covidinfo-confirmed">
                        Cases: {numeral(country.cases).format("0,0")}
                    </div>
                    <div className="covidinfo-recovered">
                        Recovered: {numeral(country.recovered).format("0,0")}
                    </div>
                    <div className="covidinfo-deaths">
                        Deaths: {numeral(country.deaths).format("0,0")}
                    </div>
                </div>
            </Popup>
        </Circle>
    ));